/* index.html */
/* main */

.div-start{
    width: 100%;
    border: solid black 1px;  
}


/* section */
.section__back{
    width: 100%;
    height: 40px;
    font-size: 14px;
    border-bottom: 1px solid #f1f3f5;
}
.section__content{
    padding: 0px 32px 0px 32px;
    width: 1200px;
    height: 20px;
    margin: 0 auto;
}
.section__content li:nth-child(1){
    margin: 0px 2px 0px 0px;
}
.section__content li:nth-child(2){
    width: 1px;
    height: 12px;
    margin: 0px 8px;
    background: #d5dbe2
}


/* header */
.header{
  width: 100%;
  height: 64px;
  color: #4a4a4a;
  background-color: #ffffff;
  z-index: 72;
  position: sticky;
  top:0;
  box-shadow: 0 2px 4px 0 hsl(0deg 0% 81% / 50%);
}
.header__content{
  width: 1200px;
  height: 100%;
  margin: 0 auto;
  padding: 0px 32px 0px 32px;
}
.header__logo{
  width: 140px;
  height: 100%;
  margin-right: 10px;
}
.header__menu{
  width: 100%;
  height: 100%;
  position: relative;
}

/* hover 색 변화 */
.header__menu > div:hover > a > span{
  color:#1dc078;
}


/* 첫번째 메뉴 보여주기 */
.header__menu-course{
  width: 180px;
  box-shadow: 8px 5px 8px 1px rgb(0 10 18 / 10%), 0 0 0 1px rgb(0 10 18 / 10%);
  padding: 0.5rem;
  color: #4a4a4a;
  background-color: #ffffff;
  line-height: 2;
  position: absolute;
  top:60px;
  left:10px;
  z-index: 74;
  display: none;
}
/* 첫번째 메뉴 보여주기 */
.header__menu > div:nth-child(1):hover > .header__menu-course{
  display: block;
}


/* 네번째 메뉴 보여주기 */
.header__menu-community{
  width: 180px;
  box-shadow: 8px 5px 8px 1px rgb(0 10 18 / 10%), 0 0 0 1px rgb(0 10 18 / 10%);
  padding: 0.5rem;
  color: #4a4a4a;
  background-color: #ffffff;
  line-height: 2;
  position: absolute;
  top:60px;
  left:200px;
  z-index: 74;
  display: none;
}
/* 네번째 메뉴 보여주기 */
.header__menu > div:nth-child(4):hover > .header__menu-community{
  display: block;
}

/* 다섯번째 메뉴 보여주기 */
.header__menu-inflearn{
  width: 180px;
  box-shadow: 8px 5px 8px 1px rgb(0 10 18 / 10%), 0 0 0 1px rgb(0 10 18 / 10%);
  padding: 0.5rem;
  color: #4a4a4a;
  background-color: #ffffff;
  line-height: 2;
  position: absolute;
  top:60px;
  left:210px;
  z-index: 74;
  display: none;
}
/* 네번째 메뉴 보여주기 */
.header__menu > div:nth-child(5):hover > .header__menu-inflearn{
  display: block;
}


.header__menu-search{
  padding: 8px;
  width: 164px;
  position: relative;
}
.header__menu-search input{
  width: 148px;
  height: 36px;
  padding: 5px 9px;
  border-radius: 3px;
  box-shadow: none;
  background: #f6f6f6;
  color: #5f5f5f;
  border: 1px solid transparent;
  border-radius: 3px;
  font-size: 16px;
}
.header__menu-search input:hover{
  border-color: #dbdbdb;  
}
.header__menu-search input:focus{
  border-color: #d4d4d4;
}
.header__menu-search input div{
  width: 100%;
  height: 100%;
}

.header__menu-search .fa-search{
  position: absolute;
  right: 20px;
  top:24px;
}


.header__menu-cart{
  position: relative;
}
