/* footer */
.footer{
  width: 100%;
  height: 438px;
  background: #303740;
  padding: 0;
  margin-top: 10px;
}
.footer-wrapper{
  display: flex;
  max-width: 1152px;
  margin: 0 auto;
  flex-direction: column;
  height: 100%;
}
.footer-top{
  display: flex;
  justify-content: space-between;
  padding: 32px 20px;
  border-bottom: 1px solid #444;
  height: 261px;
}
.footer-menu-list{
  margin-right: 20px;
}
.list_title{
  font-weight: 700;
  font-size: 14px;
  color: #eee;
  padding: 16px 0;
  line-height: 1.43;
}
.menu_sublist{
  display: flex;
  flex-direction: column;
}
.menu_sublist a{
  color: #bdbdbd;
  font-size: 14px;
  line-height: 1.43;
  margin-bottom: 7px;
}

.footer-bottom{
  display: flex;
  height: 176px;
  padding: 32px 20px;
  justify-content: space-between;
}

.footer-bottom__left{
  display: flex;
  flex-direction: column;
}


#icon_brand_logo{
  font-size: 12px;
  color: #bdbdbd;
  line-height: 1.5;
  font-weight: 700;
}

.footer_info_top{
  margin-bottom: 8px;
}
.footer_info_top a{
  fill:#1dc078;
}



.footer_info_top span{
  display: inline-block;
  content: "|";
  color: #bdbdbd;
  margin: 0 2px;
  font-size: 12px;
}
.info-dropdown{
  max-height: 54px;
  margin-bottom: 8px;
  font-size: 12px;
  line-height: 1.5;
  color: #bdbdbd;
}

.footer-bottom__right{
  width: 260px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: 20px;
}
.footer-bottom__right a{
  display: inline-block;
  width: 34px;
  height: 34px;
  border-radius: 100%;
  background: #bdbdbd;
  text-align: center;
  line-height: 35px;
}



/* footer */
.detail-footer{
  width: 100%;
  background: #303740;
  padding: 0;
  margin-top: 10px;
}


/* 로그인 모달창 */
.login-black-bg-modal {
  width : 100%;
  height : 100%;
  position : fixed;
  background : rgba(11,19,30,.37);
  z-index : 80;
  transition: all 1s;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  visibility: hidden;
  opacity: 0;
}
.login-white-bg-modal {
  background: white;
  border-radius: 6px;
  padding: 24px;
  margin: auto;
  width: 360px;
  height: 432px;
} 

.show-modal{
  visibility: visible;
  opacity: 1;
}


.login-close-btn{
  display: flex;
  align-items: center;
}
.login-close-btn svg{
  margin-left: auto;
  width: 16px;
  height: 12;
  cursor: pointer;
}

.login-logo{
  display: flex;
  align-items: center;
  margin: 12px 0 24px;
}
.brand_logo{
  width: 135px;
  height: 24px;
  margin: auto;
}
.brand_logo path{
  fill: #1dc078;
}

.form-control{
  margin-bottom: 12px;
  width: 312px;
  margin-top: 0;
  padding: 13px 12px;
  height: 48px;
  line-height: 1.47;
  font-size: 15px;
  border: 1px solid #dee2e6;
  letter-spacing: -.3px;
  border-radius: 4px;
  background-color: #fff;
}
.login-btn{
  border-color: #00c471;
  font-weight: 700;
  background-color: #00c471;
  margin: 12px 0;
  width: 312px;
  height: 48px;
  border: 1px solid;
  color: #fff;
  padding: 0 12px;
  line-height: 1.43;
  font-size: 14px;
  letter-spacing: -.3px;
}


.sign-in-modal__more-action{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 24px;
}
.more-action__text{
  line-height: 1.38;
  letter-spacing: -.3px;
  font-size: 13px;
  font-weight: 400;
  color: #616568;
  border-bottom: 1px solid #858a8d;
  cursor: pointer;
}

.more-action-divider{
  margin: 0 8px;
  width: 1px;
  height: 10px;
  background-color: #858a8d;
  pointer-events: none;
}

.sign-in-modal__social-sign-in{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.social-sign-in__line{
  position: relative;
  bottom: -8px;
  display: block;
  margin: 0;
  width: 100%;
  height: 1px;
  background-color: #a0a0a0;
  border: none;
}
.social-sign-in__title{
  padding: 0 8px;
  margin-bottom: 16px;
  font-size: 11px;
  line-height: 16px;
  letter-spacing: -.3px;
  color: #abb0b5;
  z-index: 11;
  background-color: #fff;
}

.social__sign-in-buttons{
  display: flex;
  margin: auto;
  column-gap: 12px;
}

.social__button{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
  border: 0;
  border-radius: 4px;
  box-shadow: 0 1px 2px 0 rgb(0 0 0 / 20%);
  cursor: pointer;
}
.social__button--Kakao {
  background-color: #fae500;
}
.social__button--Google {
  background-color: #f8f8f8;
}
.social__button--Github {
  background-color: #3c4043;
}
.social__button--Apple {
  background-color: #fff;
}








